import React from 'react'
import { Link } from 'react-router-dom'
import { Avatar, Button,  Menu } from 'antd'
import { UserOutlined } from '@ant-design/icons'
import { Viewer } from '../../types'
import { displaySuccessNotification, displayErrorMessage, iconColor } from '../../../lib/utils'
import { LOG_OUT } from '../../graphql/mutations'
import { LogOut as LogOutData } from '../../graphql/mutations/LogOut/__generated__/LogOut'
import { useMutation } from 'react-apollo'

const { Item, SubMenu } = Menu

interface Props {
	viewer: Viewer;
	setViewer: (viewer: Viewer) => void;
}

export const MenuItems = ({ viewer, setViewer }: Props) => {

	const [logOut] = useMutation<LogOutData>(LOG_OUT, {
		onCompleted: (data) => {
			if (data && data.logOut){
				setViewer(data.logOut) // since logOut returns empty Viewer object
				sessionStorage.removeItem('token')
				displaySuccessNotification('You\'ve succesfully logged out!')
			}
		},
		onError: () => {
			displayErrorMessage('Sorry! We were unable to log you out. Please try again later.')
		}
	})

	const handleLogOut = () => {
		logOut()
	}

	const subMenuLogin = viewer.id && viewer.avatar ? (
		<SubMenu title={<Avatar src={viewer.avatar} />}>
			<Item key={'/user/'}>
				<UserOutlined style={{ color: iconColor }} />
				Profile
			</Item>
			<Item key='logout'>
				<div onClick={handleLogOut}>
					Log Out
				</div>
			</Item>
		</SubMenu>
	) : (
		<Item>
			<Link to='/login'>
				<Button type='primary'>Sign In</Button>
			</Link>
		</Item>
	)
	return(
		<Menu mode='horizontal' selectable={false} className='menu'>
			<Item key='/host'>
				<Link to='/host'>
					Host
				</Link>
			</Item>
			{subMenuLogin}
		</Menu>
	)
}