import React, { useState } from 'react'
import { Typography, Layout, Form, Input, InputNumber, Radio, Upload, Button } from 'antd'
import { BankOutlined, HomeOutlined, UploadOutlined } from '@ant-design/icons'
import FormComponentProps, { FormInstance } from 'antd/lib/form'
import { UploadChangeParam } from 'antd/lib/upload'
import { Viewer } from '../../lib/types'
import { Link, Redirect } from 'react-router-dom'
import { ListingType } from '../../lib/graphql/globalTypes'
import { iconColor, displayErrorMessage, displaySuccessNotification } from '../../lib/utils'
import { useMutation } from '@apollo/react-hooks'
import { HOST_LISTING } from '../../lib/graphql/mutations'
import { 
	HostListing as HostListingData,
	HostListingVariables
} from '../../lib/graphql/mutations/HostListing/__generated__/HostListing'

const { Content } = Layout
const { Text, Title } = Typography
const { Item } = Form

interface Props {
	viewer: Viewer;
}

const beforeImageUpload = (file: File) => {
	const fileIsValidImage = file.type === 'image/jpeg' || file.type === 'image/png'
	const fileIsValidSize = file.size / 1024 / 1024 < 1

	if (!fileIsValidImage){
		displayErrorMessage('TYPEYou are only able to upload valid JPG or PNG files!')
		return false
	}
	if (!fileIsValidSize){
		displayErrorMessage('You are only able to upload valid images under 1MB in size!')
		return false
	}

	return fileIsValidImage && fileIsValidSize
}

const getBase64Value = (
	img: File | Blob,
	callback: (imageBase64Value: string) => void
) => {
	const reader = new FileReader()
	reader.readAsDataURL(img)
	reader.onload = () => {
		callback(reader.result as string)
	}
}

export const Host = ({ viewer }: Props) => {

	const [imageLoading, setImageLoading] = useState(false)
	const [imageBase64Value, setImageBase64Value] = useState<string | null>(null)

	const [hostListing, { loading, data }] = useMutation<
		HostListingData,
		HostListingVariables
	>(HOST_LISTING, {
		onCompleted: () => {
			displaySuccessNotification('You have successfully created your listing!')
		},
		onError: () => {
			displayErrorMessage('Sorry! We were not able to create your listing.')
		}
	})

	const [form] = Form.useForm()

	const handleImageUpload = (info: UploadChangeParam) => {
		const { file } = info

		if (file.status === 'uploading'){
			setImageLoading(true)
			return
		}

		if (file.status === 'done' && file.originFileObj){
			getBase64Value(file.originFileObj, imageBase64Value => {
				setImageBase64Value(imageBase64Value)
				setImageLoading(false)
			})
		}

	}

	const handleHostListing = (values: any) => {
		console.log('submitted form', values)
		const fullAddress = `${values.address}, ${values.city}, ${values.state}, ${values.postalCode}`
		const input = {
			...values,
			address: fullAddress,
			image: imageBase64Value,
			price: values.price * 100
		}

		delete input.city
		delete input.state
		delete input.postalCode

		hostListing({
			variables:{
				input
			}
		})

	}

	const handleHostListingFailure = ({ errorFields }: any) => {
		form.scrollToField(errorFields[0].name)
	}

	if (data && data.hostListing){
		return <Redirect to={`/listing/${data.hostListing.id}`} />
	}


	if (!viewer.id || !viewer.hasWallet){
		return (
			<Content className='host-content'>
				<div className="host__form-header">
					<Title level={4} className='host__form-title'>
						You'll have to be signed in and connected with Stripe to host a listing.
					</Title>
					<Text type='secondary'>
						We only allow users who've signed in to our application and have connected with
						Stripe to host new listings. You can sign in at the{" "}
						<Link to='/login'>/login</Link> page and connect with Stripe shortly after.
					</Text>
				</div>
			</Content>
		)
	}

	if (loading) {
		return (
			<Content className='host-content'>
				<div className="host__form-header">
					<Title level={3} className='host__form-title'>
						Please wait!
					</Title>
					<Text type='secondary'>We're creating your listing now.</Text>
				</div>
			</Content>
		)
	}

	return (
		<Content className='host-content'>
			<Form
				layout='vertical'
				name='host__form'
				form={form}
				onFinish={handleHostListing}
				onFinishFailed={handleHostListingFailure}
			>
				<div className="host__form-header">
					<Title level={3} className='host__form-title'>
						Hi! Let's get started listing your place.
					</Title>
					<Text type='secondary'>
						In this form, we'll collect some basic and additional information about your listing.
					</Text>
				</div>

				<Form.Item 
					name='type'
					label='Home Type'
					rules={[{ required: true, message: 'Please select a home type!' }]}	
				>
					<Radio.Group>
						<Radio.Button value={ListingType.APARTMENT}>
							<BankOutlined style={{ color: iconColor }} /> 
							<span> Apartment</span>
						</Radio.Button>
						<Radio.Button value={ListingType.HOUSE}>
							<HomeOutlined style={{ color: iconColor }} />
							<span> House</span>
						</Radio.Button>
					</Radio.Group>
				</Form.Item>

				<Form.Item
					name='numOfGuests'
					label='Max # of Guests'
					rules={[{ required: true, message: 'Please input a # of guests!' }]}
				>
					<InputNumber min={1} placeholder='4' />
				</Form.Item>


				<Form.Item 
					name='title'
					label='Title'
					extra='Max character count of 45'
					rules={[{ required: true, message: 'Please input a Title!' }]}
				>
					<Input maxLength={45} placeholder='The iconic and luxurious Bel-Air mansion' />
				</Form.Item>

				<Form.Item
					name='description'
					label='Description of listing'
					extra='Max character count of 400'
					rules={[{ required: true, message: 'Please input a Description!' }]}
				>
					<Input.TextArea 
						rows={3}
						maxLength={400}
						placeholder='Modern, clean, and iconic home of the Fresh Prince. 
						Situated in the heart of Bel-Air, Los Angeles.'
					/>
				</Form.Item>

				<Form.Item
					name='address'
					label='Address'
					rules={[{ required: true, message: 'Please input an Address!' }]}	
				>
					<Input placeholder='251 North Bristol Avenue' />
				</Form.Item>

				<Form.Item
					name='city'
					label='City/Town'
					rules={[{ required: true, message: 'Please input a City/Town!' }]}
				>
					<Input placeholder='Los Angeles' />
				</Form.Item>

				<Form.Item
					name='state'
					label='State/Province'
					rules={[{ required: true, message: 'Please input a State/Province!' }]}
				>
					<Input placeholder='California' />
				</Form.Item>

				<Form.Item 
					name='postalCode'
					label='Zip/Postal Code'
					rules={[{ required: true, message: 'Please input a Zip/Postal Code!' }]}
				>
					<Input placeholder='Please enter a zip code for your listing!' />
				</Form.Item>

				<Form.Item 
					name='image'
					label='Image'
					extra='Images have to be under 1MB in size and of type JPG or PNG'
					rules={[{ required: true, message: 'Please upload an image for your listing!' }]}
				>
					<div className='host__form-image-upload'>
						<Upload 
							name='image'
							listType='picture-card'
							showUploadList={false}
							action='https://www.mocky.io/v2/5cc8019d300000980a055e76'
							beforeUpload={beforeImageUpload}
							onChange={handleImageUpload}
						>
							{imageBase64Value ? (
								<img src={imageBase64Value} alt='Listing' />
							) : (
								<div>
									<UploadOutlined type={imageLoading ? 'loading' : 'plus'} />
									<div className='ant-upload-text'>Upload</div>
								</div>
							)}

						</Upload>
					</div>
				</Form.Item>

				<Form.Item 
					name='price'
					label='Price'
					extra='All prices in $USD/day'
					rules={[{ required: true, message: 'Please input a Price!' }]}
				>
					<InputNumber min={0} placeholder='120' />
				</Form.Item>

				<Form.Item>
					<Button type='primary' htmlType='submit'>Submit</Button>
				</Form.Item>

			</Form>
		</Content>
	)
}

