import React from 'react'
import { Modal, Button, Divider, Typography, Card } from 'antd'
import moment, { Moment } from 'moment'
import { KeyOutlined } from '@ant-design/icons'
import { formatListingPrice, displayErrorMessage, displaySuccessNotification } from '../../../../lib/utils'
import { CardElement, injectStripe, ReactStripeElements } from 'react-stripe-elements'
import { CREATE_BOOKING } from '../../../../lib/graphql/mutations'
import {
	CreateBooking as CreateBookingData,
	CreateBookingVariables,
	CreateBooking
} from '../../../../lib/graphql/mutations/CreateBooking/__generated__/CreateBooking'
import { useMutation } from '@apollo/react-hooks'

interface Props {
	price: number
	checkInDate: Moment
	checkOutDate: Moment
	modalVisible: boolean
	setModalVisible: (modalVisible: boolean) => void
	id: string
	clearBookingData: () => void
	handleListingRefetch: () => Promise<void>
}

const { Paragraph, Text, Title } = Typography


export const ListingCreateBookingModal = ({ 
	modalVisible,
	setModalVisible,
	price,
	checkInDate,
	checkOutDate,
	stripe,
	id,
	clearBookingData,
	handleListingRefetch
}: Props & ReactStripeElements.InjectedStripeProps) => {
	
	const daysBooked = checkOutDate.diff(checkInDate, 'days') + 1
	const listingPrice = daysBooked * price
	
	const [createBooking, { loading }] = useMutation<
		CreateBookingData,
		CreateBookingVariables
	>(CREATE_BOOKING, {
		onCompleted: () => {
			clearBookingData()
			displaySuccessNotification('You have successfully booked the listing!',
			'Booking history can always be found in your User page.')
			handleListingRefetch()
		},
		onError: () => {
			displayErrorMessage('Sorry! We were not able to successfully book the listing.')
		}
	})

	const handleCreateBooking = async () => {
		if (!stripe){
			return displayErrorMessage('Sorry! We were not able to connect with Stripe. Please try again.')
		}

		let { token: stripeToken, error } = await stripe.createToken()
		
		if (stripeToken){
			createBooking({
				variables: {
					input: {
						id,
						source: stripeToken.id,
						checkIn: moment(checkInDate).format('YYYY-MM-DD'),
						checkOut: moment(checkOutDate).format('YYYY-MM-DD')
					}
				}
			})
		} else {
			displayErrorMessage( error && error.message ? error.message : 'Sorry! Unable to book the listing.')
		}
	}
	

	return (
		<Modal
			visible={modalVisible}
			centered
			footer={null}
			onCancel={() => {
				setModalVisible(false)
			}}
		>
			<div className="listing-booking-modal">
				<div className="listing-booking-modal__intro">
					<Title className='listing-booking-modal__intro-title'>
						<KeyOutlined />
					</Title>
					<Title level={3} className='listing-booking-modal__intro-title'>
						Book your trip
					</Title>
					<Paragraph>
						Enter your payment information to book the listing from the dates between{" "}
						<Text mark strong>
							{moment(checkInDate).format('MMMM Do YYYY')}
						</Text>
						and{" "}
						<Text mark strong>
							{moment(checkOutDate).format('MMMM Do YYYY')}
						</Text>
						, inclusive.
					</Paragraph>
				</div>

				<Divider />

				<div className="listing-booking-modal__charge-summary">
					<Paragraph>
						{formatListingPrice(price, false)} x {daysBooked} days ={" "}
						<Text strong>{formatListingPrice(listingPrice, false)}</Text>
					</Paragraph>
					<Paragraph className='listing-booking-modal__charge-summary-total'>
						Total = <Text mark>{formatListingPrice(listingPrice, false)}</Text>
					</Paragraph>
				</div>

				<Divider />

				<div className="listing-booking-modal__stripe-card-section">
					<CardElement hidePostalCode className='listing-booking-modal__stripe-card' />
					<Button 
						size='large'
						type='primary'
						className='listing-booking-modal__cta'
						onClick={handleCreateBooking}
						loading={loading}
					>
						Book
					</Button>
				</div>
			</div>
		</Modal>
	)
}

export const WrappedListingCreateBookingModal = injectStripe(ListingCreateBookingModal)