import React, { useState } from 'react'
import { USER } from '../../lib/graphql/queries'
import { User as UserData, UserVariables } from '../../lib/graphql/queries/User/__generated__/User'
import { useQuery } from '@apollo/react-hooks'
import { RouteComponentProps } from 'react-router-dom'
import { Row, Col, Layout } from 'antd'
import { UserProfile, UserBookings, UserListings } from './components'
import { Viewer } from '../../lib/types'
import { ErrorBanner, PageSkeleton } from '../../lib/components'

interface MatchParams {
	id: string
}

interface Props {
	viewer: Viewer
	setViewer: (viewer: Viewer) => void
}

const { Content } = Layout

const PAGE_LIMIT = 4

export const User = ({ viewer, setViewer, match }: Props & RouteComponentProps<MatchParams>) => {

	const [listingsPage, setListingsPage] = useState(1)
	const [bookingsPage, setBookingsPage] = useState(1)


	const { data, loading, error, refetch } = useQuery<UserData, UserVariables>(USER, {
		variables: {
			id: match.params.id,
			bookingsPage,
			listingsPage,
			limit: PAGE_LIMIT
		}
	})

	if (loading){
		return (
			<Content className='user'>
				<PageSkeleton />
			</Content>
		)
	}

	if (error){
		return(
			<Content className='user'>
				<ErrorBanner description="This user may not exist or we've encountered and error. Please try again" />
				<PageSkeleton />
			</Content>
		)
	}
	
	const handleUserRefetch = async () => {
		await refetch()
	}

	const viewerIsUser = viewer.id === match.params.id
	
	const user = data ? data.user : null
	const userProfileElement = user ? (
		<UserProfile 
			user={user}	
			viewer={viewer}
			viewerIsUser={viewerIsUser}
			setViewer={setViewer}
			handleUserRefetch={handleUserRefetch}
		/>
	 ) : null
	
	const userListings = user ? user.listings : null
	const userListingsElement = userListings ? (
		<UserListings 
			userListings={userListings}
			listingsPage={listingsPage}
			limit={PAGE_LIMIT}
			setListingsPage={setListingsPage}/> 
	) : null
	
	const userBookings = user ? user.bookings : null
	const userBookingsElement = userBookings ? (
		<UserBookings 
			userBookings={userBookings}
			bookingsPage={bookingsPage}
			limit={PAGE_LIMIT}
			setBookingsPage={setBookingsPage}
		/> 
	) : null

	const stripeError = new URL(window.location.href).searchParams.get('stripe_error')
	const stripeErrorBanner = stripeError ? (
		<ErrorBanner description='We had an issue connecting with Stripe. Try again later.' />
	) : null

	return (
		<Content className='user'>
			{stripeErrorBanner}
			<Row gutter={12} justify='space-between'>
				<Col xs={24}>{userProfileElement}</Col>
				<Col xs={24}>
					{userListingsElement}
					{userBookingsElement}
				</Col>
			</Row>
		</Content>
	)
}